<script>
import { mapActions } from 'vuex'

export default {
  name: 'IFXLogout',
  data: function() {
    return {
      success: false,
      failure: false,
      message: "",
      routeInfo: {name: "Home"}
    }
  },
  methods: {
    ...mapActions([
      'showMessage',
      'logout'
    ])
  },
  mounted () {
    this.logout()
      .then(() => this.success = true)
      .catch(() => this.failure = true)
  },
}
</script>

<template>
  <v-container fluid>
    <v-row align="center" justify="center">
        <v-col v-if="success">
          <h1>Logout Successful</h1>
          <p>You are being routed...</p>
        </v-col>
        <v-col v-else-if="failure">
          <h1>Logout Unsucessful</h1>
          <p>An error occurred while attempting to log you out. Here is the error message:</p>
          <p>{{message}}</p>
          <p>For more information, please contact <a href="mailto:informatics@rc.fas.harvard.edu">.</a></p>
          <p>You are being routed...</p>
        </v-col>
        <v-col v-else>
          <h1>Logging you out...</h1>
        </v-col>
    </v-row>
  </v-container>
</template>
